.example {
    background-color: #EAEAEA;
    display: flex;
    flex-direction: column;
    @media (min-width: @tablet-width) {
      background-color: #ffffff
    }
    @media (min-width: @desktop-width) {
    flex-direction: row;
    background-color: #EAEAEA;
    }
}

.example__heading {
  font-size: 36px;
  line-height: 40px;
}

.example__description {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.weightday__number {
  font-size: 24px;
  line-height: 24px;
}

.weightday__description {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 100%;
  color: #444444;
}

.example__heading2 {
  font-size: 14px;
  line-height: 20px;
}