.directions-list__item {
  background-color: #F2F2F2;
}

.directions__direction{
  font-size: 24px;
  line-height: 37px;
}

.directions__description{
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.directions_button{
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
}

.directions-list {
  display: flex;
  flex-direction: column;
  @media (min-width: @desktop-width) {
    flex-direction: row;
  }
}